"use client";

import { useState } from "react";

interface EditFormProps {
  initialHomeTeam: string;
  initialAwayTeam: string;
}

export default function EditForm({ initialHomeTeam, initialAwayTeam }: EditFormProps) {
  const [homeTeam, setHomeTeam] = useState(initialHomeTeam);
  const [awayTeam, setAwayTeam] = useState(initialAwayTeam);

  return (
    <div style={{paddingTop: '1rem', display: 'none'}}>
      <h3>Edit Event:</h3>
      <p style={{ marginTop: '0.35rem' }}>Name 1:</p>
      <input
        type="text"
        value={homeTeam}
        onChange={(e) => {
          setHomeTeam(e.target.value);
          console.log("Home team changed to:", e.target.value);
        }}
        className="border p-1 rounded"
      />
      <p style={{ marginTop: '0.35rem' }}>Name 2:</p>
      <input
        type="text"
        value={awayTeam}
        onChange={(e) => {
          setAwayTeam(e.target.value);
          console.log("Away team changed to:", e.target.value);
        }}
        className="border p-1 rounded"
      />
    </div>
  );
}
